import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

export type NotificationType =
  | 'primary'
  | 'default'
  | 'success'
  | 'error'
  | 'warning'
  | 'feature';

@Component({
  tag: 'bh-notification',
  styleUrl: 'bh-notification.css',
  shadow: true,
})
export class BhNotification {
  /**
   * Type of notification (determines color and icon)
   */
  @Prop() type: NotificationType = 'default';

  /**
   * Notification title
   */
  @Prop() notificationTitle: string = '';

  /**
   * Notification description
   */
  @Prop() description: string = '';

  /**
   * Text for the action button
   */
  @Prop() actionText: string = '';

  /**
   * Text for the dismiss button
   */
  @Prop() dismissText: string = '';

  /**
   * Whether to show the close button
   */
  @Prop() dismissible: boolean = true;

  /**
   * Custom icon name (overrides type-based icon)
   */
  @Prop() icon: string = '';

  /**
   * Emitted when action button is clicked
   */
  @Event() bhAction!: EventEmitter<void>;

  /**
   * Emitted when dismiss button is clicked
   */
  @Event() bhDismiss!: EventEmitter<void>;

  /**
   * Emitted when close button is clicked
   */
  @Event() bhClose!: EventEmitter<void>;

  private getIconName(): string {
    if (this.icon) return this.icon;

    switch (this.type) {
      case 'primary':
        return 'new_releases';
      case 'success':
        return 'check_circle';
      case 'error':
        return 'error';
      case 'warning':
        return 'warning';
      case 'feature':
        return 'rocket_launch';
      case 'default':
        return 'info';
      default:
        return 'info';
    }
  }

  private getIconColor(): 'brand' | 'gray' | 'error' | 'warning' | 'success' | 'highlight' {
    switch (this.type) {
      case 'primary':
        return 'brand';
      case 'success':
        return 'success';
      case 'error':
        return 'error';
      case 'warning':
        return 'warning';
      case 'feature':
        return 'highlight';
      case 'default':
      default:
        return 'gray';
    }
  }

  private getIconStyle(): 'outlined' | 'filled' {
    switch (this.type) {
      case 'primary':
        return 'outlined';
      case 'success':
        return 'filled';
      case 'error':
        return 'filled';
      case 'warning':
        return 'filled';
      case 'feature':
        return 'outlined';
      case 'default':
      default:
        return 'outlined';
    }
  }

  private handleAction = (): void => {
    this.bhAction.emit();
  };

  private handleDismiss = (): void => {
    this.bhDismiss.emit();
  };

  private handleClose = (): void => {
    this.bhClose.emit();
  };

  render() {
    const notificationClasses = {
      notification: true,
      [`notification-${this.type}`]: true,
    };

    return (
      <div class={notificationClasses} role="alert" part="container">
        <div class="notification-content">
          {/* Icon */}
          <div class="notification-icon-wrapper">
            <bh-featured-icon
              icon={this.getIconName()}
              color={this.getIconColor()}
              size="sm"
              iconStyle={this.getIconStyle()}
            />
          </div>

          {/* Text Content */}
          <div class="notification-text">
            <div class="notification-text-wrapper">
              {this.notificationTitle && (
                <h3 class="notification-title">{this.notificationTitle}</h3>
              )}
              {this.description && (
                <p class="notification-description">{this.description}</p>
              )}
            </div>

            {/* Actions */}
            {(this.actionText || this.dismissText) && (
              <div class="notification-actions">
                {this.dismissText && (
                  <bh-button
                    hierarchy="quaternary"
                    size="sm"
                    label={this.dismissText}
                    icon="none"
                    onBhClick={this.handleDismiss}
                  />
                )}
                {this.actionText && (
                  <bh-button
                    hierarchy="quaternary"
                    size="sm"
                    label={this.actionText}
                    icon="none"
                    onBhClick={this.handleAction}
                  />
                )}
              </div>
            )}
          </div>
        </div>

        {/* Close Button */}
        {this.dismissible && (
          <button
            class="notification-close"
            onClick={this.handleClose}
            aria-label="Close notification"
          >
            <span class="material-symbols-outlined close-icon">close</span>
          </button>
        )}
      </div>
    );
  }
}